import React, { useEffect, useState } from "react";
import Pagination from "../../components/Pagination";
import Item from "./child/Item";
import { Link } from "react-router-dom";
import Delete from "./child/Delete";
import ModalUID from "../../components/ModalUID";
import Datetime from 'react-datetime';

import "./style.css";

export default function StaffPage() {
  const [state, setState] = useState({
    modal: {
      componentAction: null,
      title: "",
      method: null,
    },
  });

  const { modal } = state;

  function handleDelete() {
    setState({
      ...state,
      modal: {
        componentAction: <Delete></Delete>,
        title: "Xóa bình luận",
        method: "REMOVE",
      },
    });
  }

  return (
    <div className="container-fluid">
      <div style={{ display: "flex", justifyContent: "start" }}>
        <h4 className="h4 title_content mb-0 text-gray-800">
          Bình luận bài viết
        </h4>{" "}
      </div>

      <div className="card shadow mb-4">
        <div className="card-header py-3">
          <div className="row" style={{ justifyContent: "start" }}>
            <form>
              <div className="input-group mb-6" style={{ padding: "0px 20px" }}>
                <input
                  type="search"
                  name="txtSearch"
                  className="form-control"
                  placeholder="Tìm bình luận"
                  style={{ maxWidth: "400px", minWidth: "200px" }}
                />
                <div className="input-group-append">
                  <button className="btn btn-primary" type="submit">
                    <i className="fa fa-search" />
                  </button>
                </div>
              </div>
            </form>
            <form>
              <select name="" id="" className="form-control">
                <option value="">-- Tất cả bài viết -- </option>
                <option value="">Bài viết A</option>
              </select>
            </form>
            <form style={{marginRight : "8px" , marginLeft : "8px"}}> 
            <Datetime  value={"19/09/2022"} timeFormat = {false} dateFormat ="DD/MM/YYYY" />

            </form>
            <button
              type="button"
              class="btn btn-primary-no-background btn-sm"
              data-toggle="modal"
              data-target="#showListProduct"
              // style="margin-left: 10px;"
            >
              <i class="fas fa-filter"></i>
              <span class="text">&nbsp;Lọc</span>
            </button>
          
          </div>
        </div>
        <div className="card-body">
          <div className="table-responsive">
            <div
              id="dataTable_wrapper"
              className="dataTables_wrapper no-footer"
            >
              <table
                className="table no-footer dataTable"
                id="dataTable"
                width="100%"
                cellSpacing={0}
                aria-describedby="dataTable_info"
                style={{ width: "100%" }}
              >
                <thead>
                  <tr>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Họ và tên"
                    >
                      Người đăng
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Họ và tên"
                    >
                      Bình luận
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Tên tài khoản"
                    >
                      Bình luận từ bài viết
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Số điện thoại"
                    >
                      Ngày đăng
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <Item handleDelete={handleDelete}></Item>
                  <Item handleDelete={handleDelete}></Item>
                </tbody>
              </table>
              <Pagination></Pagination>
            </div>
          </div>
        </div>
      </div>
      <ModalUID method={modal.method} title={modal.title}>
        {" "}
        {modal.componentAction}{" "}
      </ModalUID>
    </div>
  );
}
