import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";

export default function Item(props) {
  const [state, setState] = useState({
    showReply: false,
    showEdit : false
  });

  const { showReply , showEdit } = state;

  return (
    <tr className="hover-product even">
      <td>
        <div className="author-comment">
          <img
            src="https://icon-library.com/images/no-user-image-icon/no-user-image-icon-3.jpg"
            class="img-responsive"
            alt="Image"
            width="100px"
            height="100px"
          />
          <div className="info">
            <span className="name">Nguyễn Hồng Sơn</span>
            <p className="email">Sonnguyen@gmail.com</p>
          </div>
        </div>
      </td>

      <td>
        <div className="content-comment">
          <div className="name">Kiểm tra các tính năng Comment</div>
          <div className="reply-comment">
            {showEdit === true && (
              <textarea
              value={"Kiểm tra các tính năng Comment"}
                name=""
                className="form-control"
                id=""
                cols="3"
                rows="3"
              ></textarea>
            )}
          </div>
          <div className="reply-comment">
            {showReply === true && (
              <textarea
                name=""
                className="form-control"
                id=""
                cols="3"
                rows="3"
              ></textarea>
            )}
          </div>
          <div style={{ display: "flex", justifyContent: "space-between" }}>
          {showReply === false && showEdit == false && (
                    <div className="action">
                    <span
                      onClick={() => {
                        setState({...state , showReply : !showReply});
                      }}
                    >
                      Trả lời
                    </span>
                    <span
                       onClick={() => {
                        setState({...state , showEdit : !showEdit});
                      }}
                    >Sửa</span>
                    <span
                          onClick={props.handleDelete
                          }
                          data-toggle="modal"
                          data-target="#modalUID"
                     className="remove">Xóa</span>
                  </div>
            )}
      
            {showReply === true && (
              <div className="action">
                <span className="cancel"     onClick={() => {
                  setState({...state , showReply : false , showEdit : false});
                }}>Hủy</span>
                <span className="sent-comment">Gửi</span>
              </div>
            )}
                {showEdit === true && (
              <div className="action">
                <span className="cancel"     onClick={() => {
                  setState({...state , showReply : false , showEdit : false});
                }}>Hủy</span>
                <span className="sent-comment">Lưu</span>
              </div>
            )}
          </div>
        </div>
      </td>
      <td className="news-response">
        <div className="title">Comment đến từ bài viết A...</div>
        <Link to="/news/edit/1">
        <div className="watch-detail">Xem bài viết</div>
        </Link>
      </td>

      <td>20-12-2022 vào lúc 08:20</td>
    </tr>
  );
}
